<template>
  <div id="app">
    <h1 class="title">–†–∞—Å—á–µ—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –¥–æ–º–µ–Ω–æ–≤</h1>
    <div class="description">
      <h2 @click="isDescription = !isDescription">–ü–æ–∫–∞–∑–∞—Ç—å –æ–ø–∏—Å–∞–Ω–∏–µ</h2>
      <p v-if="isDescription">
        <b>V</b> - –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —è—á–µ–µ–∫ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ <i>(–æ—Ç 1 –¥–æ 40)</i><br>
        <b>H</b> - –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —è—á–µ–µ–∫ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ <i>(–æ—Ç 1 –¥–æ 40)</i><br>
        <b>P</b> - –ø–ª–æ—Ç–Ω–æ—Å—Ç—å –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è –º–∞—Ç—Ä–∏—Ü—ã <b>"1"</b> <i>(–æ—Ç 0.01 –¥–æ 0.99)</i><br>
        <br>
        –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é <b>V,H = 10, P = 0.1</b>. –ü—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∑–Ω–∞—á–µ–Ω–∏—è –≤ –ø–æ–ª—è—Ö, —Å—Ä–∞–∑—É —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ—Ç—Å—è —Ä–∞–∑–º–µ—Ä–Ω–æ—Å—Ç—å –º–∞—Ç—Ä–∏—Ü—ã –∏ –ø–ª–æ—Ç–Ω–æ—Å—Ç—å <b>"1"</b><br>
        <br>
        –ü–ª–æ—Ç–Ω–æ—Å—Ç—å <b>"1"</b> —Ä–µ–∞–ª–∏–∑—É–µ—Ç—Å—è —á–µ—Ä–µ–∑ –∞–ª–≥–æ—Ä–∏—Ç–º –∞–Ω–∞–ª–æ–≥–∏—á–Ω—ã–π <i>—Ç–∞—Å–æ–≤–∞–Ω–∏—é –§–∏—à–µ—Ä–∞-–ô–µ—Ç—Å–∞</i>.<br>
        <br>
        –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –∫–Ω–æ–ø–∫–∏ <b>"–û—Ç–æ–±—Ä–∞–∑–∏—Ç—å –º–∞—Ç—Ä–∏—Ü—É"</b> —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç—Å—è –º–∞—Ç—Ä–∏—Ü–∞, –∑–∞–ø–æ–ª–Ω–µ–Ω–Ω–∞—è "0"<br>
        –ö–ª–∏–∫ –ø–æ —è—á–µ–π–∫–µ –º–µ–Ω—è–µ—Ç –∑–Ω–∞—á–µ–Ω–∏–µ –Ω–∞ –ø—Ä–æ—Ç–∏–≤–æ–ø–æ–ª–æ–∂–Ω–æ–µ.<br>
        –ß–µ–∫–±–æ–∫—Å —Ä–∞–∑—Ä–µ—à–∞–µ—Ç/–∑–∞–ø—Ä–µ—â–∞–µ—Ç –º–æ–º–µ–Ω—Ç–∞–ª—å–Ω—ã–π —Ä–∞—Å—Å—á–µ—Ç –¥–æ–º–µ–Ω–æ–≤.<br>
        <br>
        –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –∫–Ω–æ–ø–∫–∏ <b>"–ü–æ—Å—á–∏—Ç–∞—Ç—å –¥–æ–º–µ–Ω—ã"</b>, –∑–∞–ø—É—Å–∫–∞–µ—Ç—Å—è –∞–ª–≥–æ—Ä–∏—Ç–º –ø–æ–∏—Å–∫–∞ –¥–æ–º–µ–Ω–æ–≤, —Ä–µ–∑—É–ª—å—Ç–∞—Ç –≤—ã–≤–æ–¥–∏—Ç—Å—è –≤ —Ç–∞–±–ª–∏—Ü—É.<br>
        <br>
        –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –∫–Ω–æ–ø–∫–∏ <b>"–ê–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ"</b> —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç—Å—è –º–∞—Ç—Ä–∏—Ü–∞, –∑–∞–ø—É—Å–∫–∞–µ—Ç—Å—è –ø–æ–∏—Å–∫ –¥–æ–º–µ–Ω–æ–≤, —Ä–µ–∑—É–ª—å—Ç–∞—Ç –ø–æ–ø–∞–¥–∞–µ—Ç –≤ —Ç–∞–±–ª–∏—Ü—É.<br>
        <br>
        –¢–∞–±–ª–∏—Ü–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç —Ç–æ–ª—å–∫–æ –ø–æ—Å–ª–µ–¥–Ω–∏–µ <b>10</b> –≤—ã—á–∏—Å–ª–µ–Ω–∏–π
      </p>
    </div>
    <div class="property">
      <div class="property__field">
        V: <input type="number" :class="{error: errorInput.vertical}" v-model.number="vertical" @blur="validateInputProps(vertical, 'v')">
        H: <input type="number" :class="{error: errorInput.horizontal}" v-model.number="horizontal" @blur="validateInputProps(horizontal, 'h')">
        %: <input type="number" step="0.1" :class="{error: errorInput.probability}" v-model.number="probability" @blur="validateInputProps(probability, 'p')">
        <p>–†–∞–∑–º–µ—Ä–Ω–æ—Å—Ç—å –º–∞—Ç—Ä–∏—Ü—ã 
          <span v-if="(horizontal*vertical) == 0"> –Ω–µ —É–∫–∞–∑–∞–Ω–∞</span>
          <b v-else>{{vertical}} x {{horizontal}} = {{horizontal*vertical}} —è—á–µ–µ–∫. –í–µ—Ä–æ—è—Ç–Ω–æ—Å—Ç—å {{(probability*100).toFixed(0)}}%</b>
        </p>
      </div>
      <button @click="renderMatrix()" :disabled="(horizontal*vertical) == 0">–û—Ç–æ–±—Ä–∞–∑–∏—Ç—å –º–∞—Ç—Ä–∏—Ü—É</button>
      <button @click="renderMatrixAuto()" :disabled="probability === ''">–ê–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ</button>
    </div>
    <div class="matrix">
      <div class="matrix__row" v-for="(row, i) in matrix">
        <span class="matrix__cell" v-for="(cell, j) in row"  
        @click="updateCell(cell, i, j)"
        :style="{background: test = backgroundCell(i,j)}"
        >
          {{cell}}
        </span>
      </div>      
    </div>
    <label class="is-click-calculate"><input type="checkbox" v-model="isClickCalculate" name="" id=""> –†–∞–∑—Ä–µ—à–∏—Ç—å —Ä–∞—Å—á–µ—Ç –ø—Ä–∏ –∫–ª–∏–∫–µ –ø–æ —è—á–µ–π–∫–µ</label>
    <div class="domen">
      <button class="domen__calc" @click="calculateDomens">–ü–æ—Å—á–∏—Ç–∞—Ç—å –¥–æ–º–µ–Ω—ã</button>
      <p class="domen__result">–í –≤–∞—à–µ–π –º–∞—Ç—Ä–∏—Ü–µ <b class="domen__result__num">{{countDomens}}</b> –¥–æ–º–µ–Ω–æ–≤</p>
      <table class="domen__table">
        <tr>
          <th>‚Ññ –ø/–ø</th>
          <th>–í–µ—Ä–æ—è—Ç–Ω–æ—Å—Ç—å</th>
          <th>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –¥–æ–º–µ–Ω–æ–≤ –≤ –º–∞—Ç—Ä–∏—Ü–µ</th>
          <th>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —è—á–µ–µ–∫ –≤ –º–∞—Ç—Ä–∏—Ü–µ</th>
        </tr>
        <tr v-for="calc in calculationTable" :key="calc.id">
          <td>{{calc.id}}.</td>
          <td>{{calc.probability}}</td>
          <td>{{calc.domens}}</td>
          <td>{{calc.cells}}</td>
        </tr>        
      </table>
    </div>    
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      matrix: [],
      horizontal: 10,
      vertical: 10,
      probability: 0.1,
      errorInput: {
        horizontal: "",
        vertical: "",
        probability: ""
      },
      countDomens: 0, // –ø–æ—Ö–æ–¥—É –Ω–µ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–ª —ç—Ç–æ –∑–Ω–∞—á–µ–Ω–∏–µ
      arrayBackgroundColor: [],
      groupsDomens: [],
      calculationTable: [],
      calculationNumber: 0,
      isClickCalculate: false,
      isDescription: false
    };
  },
  methods: {
    validateInputProps(value, tag) {
      switch (tag) {
        case "h": {
          if (typeof value === "number") {
            if (value < 2 || value > 40) {
              this.horizontal = "";
              this.errorInput.horizontal = true;
            } else {
              this.errorInput.horizontal = false;
            }
          } else {
            this.horizontal = "";
            this.errorInput.horizontal = true;
          }
          break;
        }
        case "v": {
          if (typeof value === "number") {
            if (value < 2 || value > 40) {
              this.vertical = "";
              this.errorInput.vertical = true;
            } else {
              this.errorInput.vertical = false;
            }
          } else {
            this.vertical = "";
            this.errorInput.vertical = true;
          }
          break;
        }
        case "p": {
          if (typeof value === "number") {
            if (value < 0.01 || value > 0.99) {
              this.probability = "";
              this.errorInput.probability = true;
            } else {
              this.errorInput.probability = false;
            }
          } else {
            this.probability = "";
            this.errorInput.probability = true;
          }
          break;
        }
      }
    },
    renderMatrix() {
      this.arrayBackgroundColor = [];
      let matrix = [];

      for (let i = 0; i < this.vertical; i++) {
        matrix[i] = [];
        for (let j = 0; j < this.horizontal; j++) {
          matrix[i][j] = 0;
        }
      }

      this.matrix = matrix;
    },
    renderMatrixAuto() {
      this.arrayBackgroundColor = [];
      let cellsCount = this.vertical * this.horizontal;

      let count = 0;

      var a = [];
      for (var i = 0; i < cellsCount; i++)
        a.push(i < cellsCount * this.probability ? 1 : 0);

      let matrix = [];

      var last = cellsCount;

      for (let i = 0; i < this.vertical; i++) {
        matrix[i] = [];
        for (let j = 0; j < this.horizontal; j++) {
          let rr = Math.floor(Math.random() * last);

          matrix[i].push(a[rr]);

          if (a[rr]) count++;

          a[rr] = a[--last];

        }
      }

      this.matrix = matrix;
      this.calculateDomens();
    },
    updateCell(value, v, h) {
      
      value = Number(!value);      
      this.$set(this.matrix[v], h, value);

      let count = 0;
      let matrix = this.matrix;
      
      for (let i = 0; i < matrix.length; i++) {
        for (let j = 0; j < matrix[i].length; j++) {
          if (matrix[i][j]) {
            ++count;
          }
        }
      };

      if (value) {
        count++;
      } else {
        count--
      }


      this.probability = Math.floor((count*100)/(this.horizontal*this.vertical))/100;

      if(this.isClickCalculate) {
        this.calculateDomens();
      }
      
    },
    // –†–∞—Å—á–µ—Ç –¥–æ–º–µ–Ω–æ–≤--------------------------------------------
    calculateDomens() {

      let matrix = this.matrix;

      let groupsDomens = []; // –º–∞—Å—Å–∏–≤ –≥—Ä—É–ø–ø —Å –¥–æ–º–µ–Ω–∞–º–∏

      let countGroup = 0;
      let closeCell = [];

      let str = "";

      for (let i = 0; i < matrix.length; i++) {
        str += "\n";

        for (let j = 0; j < matrix[i].length; ) {
          //–ø–µ—Ä–µ—Ö–æ–¥ –Ω–∞ —Å–ª–µ–¥—É—é—â–∏–π —è—á–µ–π–∫—É —Ç–æ–ª—å–∫–æ –ø–æ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—ã–º

          if (closeCell.length === 0) {
            closeCell.push({});
          } else {
            str += matrix[i][j] + " ";

            let checkCell = closeCell.filter(
              item => item.v === i && item.h === j
            );

            if (!matrix[i][j]) {
              closeCell.push({ v: i, h: j });
            }

            if (!checkCell.length) {
              if (matrix[i][j]) {
                // –ï—Å–ª–∏ –≤ —è—á–µ–π–∫–µ 1, —Ç–æ–≥–¥–∞ —Å–æ–∑–¥–∞–µ–º –¥–æ–º–µ–Ω –∏ –∑–∞–ø–æ–ª–Ω—è–µ–º –µ–≥–æ —è—á–µ–π–∫–∞–º–∏
                var domen = {};
                domen.id = ++countGroup;

                if (!domen.cells) {
                  // –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –º–∞—Å—Å–∏–≤ –¥–ª—è –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç "1"
                  domen.cells = [];
                }

                // –¥–æ–±–∞–≤–ª—è–µ–º –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—É –≤ –º–∞—Å—Å–∏–≤ —è—á–µ–µ–∫

                domen.cells.push({ v: i, h: j });

                analyze–°ells(matrix, i, j, domen, closeCell);

                groupsDomens.push(domen);
              }
            }

            j++;
          }
        }
      }
      this.countDomens = groupsDomens.length;

      this.arrayBackgroundColor = paintCells(groupsDomens);
      this.groupsDomens = groupsDomens;

      // –ü—Ä–æ–≤–µ—Ä—è–µ–º —Ç–∞–±–ª–∏—Ü—É —Ä–∞—Å—Å—á–µ—Ç–æ–≤
      // –µ—Å–ª–∏ —Ä–∞—Å—á–µ—Ç–æ–≤ –±–æ–ª—å—à–µ 10
      if (this.calculationTable.length <= 10) {
        // –î–æ–±–∞–≤–ª—è–µ–º —Ä–∞—Å—Å—á–µ—Ç –≤ —Ç–∞–±–ª–∏—Ü—É
        this.calculationTable.push({
          id: ++this.calculationNumber,
          probability: this.probability,
          domens: this.countDomens,
          cells: this.horizontal * this.vertical
        });
      } else {
        this.calculationTable.shift();
        this.calculationTable.push({
          id: ++this.calculationNumber,
          probability: this.probability,
          domens: this.countDomens,
          cells: this.horizontal * this.vertical
        });
      }
    },
    backgroundCell(v, h) {
      let arr = this.arrayBackgroundColor;

      // return "gray";

      let color;

      for (let i = 0; i < arr.length; i++) {
        if (arr[i].v == v && arr[i].h == h) {

          color = arr[i].backgroundColor;
          return color;

        } else {
          color = "#ffffff";
        }
      }
    }
  }
};

/* –ë–ª–æ–∫ –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω—ã—Ö —Ñ—É–Ω–∫—Ü–∏–π */

/* –§–£–ù–ö–¶–ò–Ø –ê–ù–ê–õ–ò–ó–ê –°–û–°–ï–î–ï–ô */

function analyze–°ells(matrix, i, j, domen, closeCell) {
  let v = i;
  let h = j;

  // –Ø—á–µ–π–∫—É, –∫–æ—Ç–æ—Ä—É—é —Å–æ–±–∏—Ä–∞–µ–º—Å—è –∞–Ω–∞–ª–∏–∑–∏—Ä–æ–≤–∞—Ç—å –Ω–∞ —Å–æ—Å–µ–¥—Å—Ç–≤–∞ —Å—Ä–∞–∑—É –¥–æ–±–∞–≤–ª—è–µ–º –≤ –≥—Ä—É–ø–ø—É –¥–æ–º–µ–Ω–∞
  closeCell.push({ v: i, h: j });

  // –°–û–°–ï–î –°–ü–†–ê–í–ê

  // –ï—Å–ª–∏ –≤ —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–µ –µ—Å—Ç—å "1"

  // –ó–¥–µ—Å—å –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å, —á—Ç–æ–±—ã —è—á–µ–π–∫–∞ –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—ã–ª–∞ –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ –º–∞—Ç—Ä–∏—Ü—ã;
  if (h < matrix[i].length) {
    // –¢–µ–ø–µ—Ä—å –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ —Å–æ—Å–µ–¥–Ω—è—è —è—á–µ–π–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç 1;

    if (matrix[v][h + 1]) {
      // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–ª—è –Ω–∞—á–∞–ª–∞ –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è —Å —è—á–µ–π–∫–∞–º–∏ –≤ –¥–æ–º–µ–Ω–µ
      if (!domen.cells.filter(item => item.v == v && item.h == h + 1).length) {
        // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–æ–±–∞–≤–ª—è–µ–º —ç—Ç—É —è—á–µ–π–∫—É –≤ –¥–æ–º–µ–Ω
        domen.cells.push({ v: v, h: h + 1 });
      }

      // –ø—Ä–µ–∂–¥–µ —á–µ–º –∑–∞–ø—É—Å–∫–∞—Ç—å —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é, –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å —Å–æ—Å–µ–¥–Ω—é—é —è—á–µ–π–∫—É –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å —è—á–µ–π–∫–∞–º–∏ –∏–∑ –º–∞—Å—Å–∏–≤–∞ —Å —É–∂–µ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã–º–∏ —è—á–µ–π–∫–∞–º–∏

      if (!closeCell.filter(item => item.v === v && item.h === h + 1).length) {
        // –∏ –∑–∞–ø—É—Å–∫–∞–µ–º —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –¥–ª—è —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–∏
        analyze–°ells(matrix, v, h + 1, domen, closeCell);
      }
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------------------------

  // –°–û–°–ï–î –°–ù–ò–ó–£

  // –ï—Å–ª–∏ –≤ —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–µ –µ—Å—Ç—å "1"

  // –ó–¥–µ—Å—å –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å, —á—Ç–æ–±—ã —è—á–µ–π–∫–∞ –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—ã–ª–∞ –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ –º–∞—Ç—Ä–∏—Ü—ã;
  if (v + 1 < matrix.length && h != null) {
    // –¢–µ–ø–µ—Ä—å –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ —Å–æ—Å–µ–¥–Ω—è—è —è—á–µ–π–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç 1;

    if (matrix[v + 1][h]) {
      // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–ª—è –Ω–∞—á–∞–ª–∞ –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è —Å —è—á–µ–π–∫–∞–º–∏ –≤ –¥–æ–º–µ–Ω–µ
      if (!domen.cells.filter(item => item.v == v + 1 && item.h == h).length) {
        // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–æ–±–∞–≤–ª—è–µ–º —ç—Ç—É —è—á–µ–π–∫—É –≤ –¥–æ–º–µ–Ω
        domen.cells.push({ v: v + 1, h: h });
      }

      // –ø—Ä–µ–∂–¥–µ —á–µ–º –∑–∞–ø—É—Å–∫–∞—Ç—å —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é, –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å —Å–æ—Å–µ–¥–Ω—é—é —è—á–µ–π–∫—É –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å —è—á–µ–π–∫–∞–º–∏ –∏–∑ –º–∞—Å—Å–∏–≤–∞ —Å —É–∂–µ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã–º–∏ —è—á–µ–π–∫–∞–º–∏

      if (!closeCell.filter(item => item.v === v + 1 && item.h === h).length) {
        // –∏ –∑–∞–ø—É—Å–∫–∞–µ–º —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –¥–ª—è —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–∏
        analyze–°ells(matrix, v + 1, h, domen, closeCell);
      }
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------------------------

  // –°–û–°–ï–î –°–õ–ï–í–ê

  // –ï—Å–ª–∏ –≤ —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–µ –µ—Å—Ç—å "1"

  // –ó–¥–µ—Å—å –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å, —á—Ç–æ–±—ã —è—á–µ–π–∫–∞ –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—ã–ª–∞ –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ –º–∞—Ç—Ä–∏—Ü—ã;
  if (h >= 0) {
    // –¢–µ–ø–µ—Ä—å –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ —Å–æ—Å–µ–¥–Ω—è—è —è—á–µ–π–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç 1;

    if (matrix[v][h - 1]) {
      // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–ª—è –Ω–∞—á–∞–ª–∞ –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è —Å —è—á–µ–π–∫–∞–º–∏ –≤ –¥–æ–º–µ–Ω–µ
      if (!domen.cells.filter(item => item.v == v && item.h == h - 1).length) {
        // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–æ–±–∞–≤–ª—è–µ–º —ç—Ç—É —è—á–µ–π–∫—É –≤ –¥–æ–º–µ–Ω
        domen.cells.push({ v: v, h: h - 1 });
      }

      // –ø—Ä–µ–∂–¥–µ —á–µ–º –∑–∞–ø—É—Å–∫–∞—Ç—å —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é, –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å —Å–æ—Å–µ–¥–Ω—é—é —è—á–µ–π–∫—É –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å —è—á–µ–π–∫–∞–º–∏ –∏–∑ –º–∞—Å—Å–∏–≤–∞ —Å —É–∂–µ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã–º–∏ —è—á–µ–π–∫–∞–º–∏

      if (!closeCell.filter(item => item.v === v && item.h === h - 1).length) {
        // –∏ –∑–∞–ø—É—Å–∫–∞–µ–º —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –¥–ª—è —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–∏
        analyze–°ells(matrix, v, h - 1, domen, closeCell);
      }
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------------------------

  // –°–û–°–ï–î –°–í–ï–†–•–£

  // –ï—Å–ª–∏ –≤ —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–µ –µ—Å—Ç—å "1"

  // –ó–¥–µ—Å—å –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å, —á—Ç–æ–±—ã —è—á–µ–π–∫–∞ –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—ã–ª–∞ –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ –º–∞—Ç—Ä–∏—Ü—ã;
  if (v - 1 >= 0 && h != null) {
    // –¢–µ–ø–µ—Ä—å –ø—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ —Å–æ—Å–µ–¥–Ω—è—è —è—á–µ–π–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç 1;

    if (matrix[v - 1][h]) {
      // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–ª—è –Ω–∞—á–∞–ª–∞ –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è —Å —è—á–µ–π–∫–∞–º–∏ –≤ –¥–æ–º–µ–Ω–µ
      if (!domen.cells.filter(item => item.v == v - 1 && item.h == h).length) {
        // –µ—Å–ª–∏ "–î–∞", —Ç–æ –¥–æ–±–∞–≤–ª—è–µ–º —ç—Ç—É —è—á–µ–π–∫—É –≤ –¥–æ–º–µ–Ω
        domen.cells.push({ v: v - 1, h: h });
      }

      // –ø—Ä–µ–∂–¥–µ —á–µ–º –∑–∞–ø—É—Å–∫–∞—Ç—å —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é, –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å —Å–æ—Å–µ–¥–Ω—é—é —è—á–µ–π–∫—É –Ω–∞ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å —è—á–µ–π–∫–∞–º–∏ –∏–∑ –º–∞—Å—Å–∏–≤–∞ —Å —É–∂–µ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã–º–∏ —è—á–µ–π–∫–∞–º–∏

      if (!closeCell.filter(item => item.v === v - 1 && item.h === h).length) {
        // –∏ –∑–∞–ø—É—Å–∫–∞–µ–º —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –¥–ª—è —Å–æ—Å–µ–¥–Ω–µ–π —è—á–µ–π–∫–∏
        analyze–°ells(matrix, v - 1, h, domen, closeCell);
      }
    }
  }
}

/* –§–£–ù–ö–¶–ò–Ø –§–û–†–ú–ò–†–û–í–ê–ù–ò–Ø –¶–í–ï–¢–û–í –î–õ–Ø –î–û–ú–ï–ù–û–í*/
function paintCells(arr) {

  // –°–æ–∑–¥–∞–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ü–≤–µ—Ç–∞ —Ñ–æ–Ω–∞
  let backgroundColor;

  let arrayPrintCells = [];

  // –û–±—Ö–æ–¥–∏–º –º–∞—Å—Å–∏–≤
  arr.map(item => {
    // –ß—Ç–æ–±—ã –≤–¥—Ä—É–≥ —è—á–µ–π–∫–∏ –Ω–µ –∑–∞–∫—Ä–∞—Å–∏–ª–∏—Å—å –±–µ–ª—ã–º —Ü–≤–µ—Ç–æ–º, –≤–æ–∑—å–º–µ–º —Ü–≤–µ—Ç–∞ —Ç–æ–ª—å–∫–æ –¥–æ #999999 - —Å–µ—Ä—ã–π
    backgroundColor = "#" + Math.floor(Math.random() * 10066329).toString(16);

    item.cells.map(cell => {
      cell.backgroundColor = backgroundColor;
      arrayPrintCells.push(cell);
    });
  });

  return arrayPrintCells;
}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000;
  margin-top: 60px;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

html {
}

body {
  margin: 0 auto;
  background: #fff;
}

.title {
}

.description {
  width: 700px;
  margin: 0 auto;
  /* background: #ccc;
  border: 1px solid #aaa; */
  font-size: 12px;
  color: #333;
  text-align: left;
  margin-bottom: 20px;
  padding: 0 20px;
}

.description h2 {
  color: #000;
  cursor: pointer;
  text-align: center;
}

.property {
  margin-bottom: 20px;
}

.property__field {
  margin-bottom: 20px;
}

.property input {
  padding: 5px 10px;
  border: 1px solid gray;
  border-radius: 5px;
}

.property button {
  padding: 10px;
  border: none;
  outline: none;
  color: #fff;
  background: green;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background: gray;
  cursor: default;
}

.matrix {
  margin-bottom: 20px;
  min-height: 100px;
}

.matrix__row {
}

.matrix__cell {
  display: inline-block;
  padding: 5px 10px;
  border: 1px dashed #000;
  cursor: pointer;
}

.matrix__cell:hover {
  background: #666;
  color: #fff;
}

.is-click-calculate {
  display: block;
  margin-bottom: 10px;
}

.domen {
  margin-bottom: 10px;
}

.domen__calc {
  padding: 10px;
  border: none;
  outline: none;
  color: #fff;
  background: rgb(57, 57, 255);
  border-radius: 5px;
  cursor: pointer;
}

.domen__result__num {
  color: green;
}

.domen__table {
  margin: 0 auto;
  border-collapse: collapse;
}

.domen__table th,
.domen__table td {
  padding: 5px 10px;
  border: 1px solid gray;
}

input.error {
  border: 1px solid red;
  box-shadow: 0 0 5px red;
}

.domens {
  background: green;
  color: white;
}

.span {
  display: inline-block;
  padding: 10px;
  margin: 5px;
}
</style>



